* {
	box-sizing: border-box;
}

a,
h1,
h2,
li,
p {
	font-family: 'Josefin Slab', serif;
}

a,
li,
p {
	font-size: 18px;
	font-weight: 300;
	line-height: 27px;
}

body {
	background-color: #f0f7f4;
	margin: 0;
}

code {
	font-size: 14px;
}

h1,
h2 {
	font-weight: 700;
}

h1 {
	color: #a2e3c4;
	font-size: 40px;
}

h2 {
	font-size: 30px;
	text-decoration: underline;
	text-decoration-color: #a2e3c4;
}

table {
	border-collapse: collapse;
}

td:first-child,
th:first-child {
	border-right: 3px solid #a2e3c4;
}

td,
th {
	height: 27px;
	padding-left: 20px;
	width: 200px;
}

th {
	border-bottom: 3px solid #a2e3c4;
}

th p {
	margin: 0;
}

#main-doc {
	flex: 1;
	height: 100vh;
	overflow-x: hidden;
	overflow-y: scroll;
	padding: 5vh 5vw;
}

#navbar {
	background-color: #3c493f;
	color: #f0f7f4;
	height: 100vh;
	overflow-y: scroll;
	padding: 10vh 0;
	width: 325px;
}

#navbar section a {
	border-bottom: 2px solid #b3bfb8;
	color: #f0f7f4;
	display: block;
	padding: 15px 2vw;
	text-decoration: none;
	width: 100%;
}

#navbar section a:hover {
	background-color: #7e8d85;
}

#navbar section a:first-child {
	border-top: 2px solid #b3bfb8;
}

#navbar header {
	padding: 0 20px;
}

#wrapper {
	display: flex;
	height: 100vh;
	width: 100vw;
}

@media only screen and (max-width: 750px) {
	h1 {
		margin: 50px 0 10px;
		text-align: center;
	}

	#main-doc {
		height: auto;
	}

	#navbar {
		border: none;
		height: auto;
		padding: 0;
		width: 100vw;
	}

	#navbar section a {
		align-items: center;
		border: none;
		border-bottom: 2px solid #b3bfb8;
		display: flex;
		flex: 1;
		justify-content: center;
		min-width: 30vw;
	}

	#navbar section a:first-child,
	#navbar section a:nth-child(2),
	#navbar section a:nth-child(3) {
		border-top: 2px solid #b3bfb8;
	}

	#navbar section a {
		text-align: center;
	}

	#navbar p {
		margin: 10px;
	}

	#navbar section {
		display: flex;
		flex-wrap: wrap;
	}

	#wrapper {
		flex-direction: column;
		height: auto;
	}
}
